var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"detail"},[_c('aside',{staticClass:"left"},[_c('h4',[_vm._v("Recent posts")]),_c('ul',[_c('li',{staticClass:"active"},[_vm._v("1024：程序员，你的身体还好吗？")]),_c('li',[_vm._v("鸿蒙 OS：使用 JavaScript 开发 Todo App UI 界面")]),_c('li',[_vm._v("职业规划：程序员的 T 字形发展攻略")]),_c('li',[_vm._v(" 前端工程师到底要学到什么程序才能去找工作？成功面试的必备要素与回答技巧 ")]),_c('li',[_vm._v("噢！原来这就是 TypeScript")])])]),_c('main',{staticClass:"middle"},[_c('h1',{staticClass:"title"},[_vm._v("1024：程序员，你的身体还好吗？")]),_c('p',{staticClass:"des"},[_vm._v("2020年10月24日 · 预计阅读时间： 1 分钟")]),_c('p',{staticClass:"tag"},[_c('span',[_vm._v("#程序员")]),_c('span',[_vm._v("#健康")])]),_c('div',{staticClass:"main"},[_c('p',[_vm._v(" 又到一年一度的 10 月 24 日程序员节了，按百度百科的定义，这一天的意义是拒绝加班： ")]),_c('blockquote',[_vm._v(" 百度百科：1024程序员节是广大程序员的共同节日。1024是2的十次方， 二进制计数的基本计量单位之一。针对程序员经常周末加班与工 作日熬夜的情况，部分互联网机构倡议每年的10月24日为1024程序员节，在这一天 建议程序员拒绝加班。 ")]),_c('p',[_vm._v(" 然而反观现实中程序员的工作与生活，这一天的意义真的达到了吗？从几何时， 程序员成了加班、996 的代名词，从以前业务做不完偶尔加班变成了现在的常态，不得不说一句俗话：这是时代的进步还是倒退？1024 这个本该关注程序员权益的问题，非但没带来任何进步，反而又像双十一成了电商推销电子产品的噱头。试问，有多少人真正关心过程序员的身心健康？鉴于此，本文将从生理和心理两方面来分析程序员职业风险，让更多人了解程序员所面临的健康问题。因为本人并非医疗专家，所以文中所提供的建议应只 作为参考，如果身体有任何异常情况请及时就医。另外，如果你热爱你 的工作并且充满了干劲，那么这篇文章可以不看。 ")]),_c('a',{attrs:{"id":"id1"}},[_vm._v("生理问题")]),_c('p',[_vm._v(" 别笑，真实生活中的程序员确实很多都是这样的。我们来逐一分析一下做程序员这一行带来的一系列的生理上的问题。 首先是颈椎。我们在写代码的时候，往往字体都比较小，这样方便观察代码的整体逻辑，不知不觉的在视力疲劳之后，头部就会往前倾，而头前倾 1 寸，颈椎压力就会增 1 倍。久而久之，颈椎就会变形，影响形象不说，还会引起脑部供血不足，造成头晕、头昏、不清醒的感觉，我本人也深受此困扰。 其次是久坐。因为写代码要求思路流畅，随便一个分心的事情都能使思路断片，便感觉懊恼不已，干脆就一直写代码，直到非要上厕所或有事的时候才起身，这就导致了久坐的问题。有研究称，久坐的危害大过吸烟，不亚于慢性自杀。 再有是熬夜。随着工作时间的加长。那么熬夜是必然的一个结果。那么熬夜的危害就不用多说了，它会造成免疫力下降，还会造成一脸的颓废相：黑眼圈、油光满面、痘痘丛生，更会引起新陈代谢减速，造成肥胖。 最后是三高。当工作忙起来的时候，我们一般就会凑合着点外卖，或者吃快餐，很少有人从家里带饭到公司里吃。因为外边餐馆的油大、口味重，再加上久坐运动少，血液里的废物不能及时的排出去，就又会导致三高和肥胖问题，而这又是猝死的元凶。 996 的工作制更加大了我们生理问题的危险，我们把更长的时间放到了盯着电脑和久坐上，无一不是在拿自己的命换钱，说程序员是青春饭也是有道理的，要解决这些问题，需要从自身习惯上下手。 ")]),_c('a',{attrs:{"id":"id2"}},[_vm._v("解决方法")]),_c('p',[_vm._v(" 如果想要从本质上解决这个问题，那就是找一份正常工作制度的公司，先别急着反驳，先想想阻止你换工作的障碍是什么？房贷？家庭？技术水平？还是其它任何原因？这些都是借口。没有人逼迫你立刻辞职，完全可以边工作边准备面试目标公司，996 至少还有 1 天是自由时间，再有平时工作也不是 100%的时间在忙，可以在这些时间里充分提升自己，进入理想的公司。短时间集中努力换取舒适的环境总比长期在一个不良的环境中要好的多，俗话说长痛不如短痛，不要给自己的不作为找借口。 而对于颈椎和久坐的问题，这个要靠外界限制和自制力。我的电脑上安装了 Strechly ，它可以设置每隔一小段时间休息 20 秒，每隔一大段时间休息 10 分钟，并且有窗口遮挡工作区，防止你继续工作，当然必要情况下也可以手动延后休息。建议是采用 20-20-20 原则，使用电子产品每20分钟就把视线移到离屏幕20英尺(约合6米)以外的地方，保持20秒或更长时间，虽然是针对眼睛的，但是我们也可以利用这 20 秒活动一下身体。另外早起或晚上下班前有时间和精力的，可以做一些有氧或无氧运动。最后在饮食上，一定要少油少盐少糖，烹饪步骤越少越少，食物应多种多样（蔬菜、肉、蛋、奶、谷物等），以保证营养。 ")]),_c('a',{attrs:{"id":"id3"}},[_vm._v("心理问题")]),_c('p',[_vm._v(" 若说程序员的生理问题严重等级为重要，那么心理问题则是重中之重。一个健康的心理，加上乐观的情绪，能够减少生理上的问题，然而程序员的工作制度和方式，给我们带来了巨大的心理压力和能量消耗。 ")]),_c('a',{attrs:{"id":"id4"}},[_vm._v("压力的产生机制")]),_c('p',[_vm._v(" 众所周知，脑力劳力和体力劳动是两个完全不同的工作方式，身体运作的方式也不一样。我想，你肯定体会过当干了很多体力活之后，晚上能倒头就睡，第二天会满血复活的情况，这是从远古的基因就决定的，人类生来并不是脑力劳动者，而是社会发展所带来的结果。脑力劳动消耗的是心理能量，当我们专注于思维逻辑编写代码时，我们的身体会作出反应，例如减少呼吸频率与深度，全身肌肉都会紧张起来，就像猎人静待猎物进入目标一样，小心翼翼的感觉，虽然没有运动，但是同样的在消耗能量。长时间不放松，体内的皮质醇就会累积，皮质醇被称为压力荷尔蒙，它会引起身体的一系列生理上的不适。首当其冲的是焦虑。 ")]),_c('a',{attrs:{"id":"id5"}},[_vm._v("焦虑")]),_c('p',[_vm._v(" 如果你曾经有过突然心慌、头晕、呼吸困难，感觉像是有大事要发生了，如果身体检查没有问题，那这个就是急性焦虑发作的感觉。它跟人们古时候面对凶猛的野兽所产生的应激反应是一样的，会促使我们身体产生这个压力荷尔蒙，然后做出要么斗要么逃（Fight or flight）的状态。这个荷尔蒙会升高血压、拉紧肌肉，把整个身体都给调动起来，以应对威胁，所以说这个时候人会发挥超长的能力。但是由于我们程序员一直是久坐状态，没法逃跑也没法战斗，那么压力荷尔蒙就会积攒在身体里，久而久之，焦虑的情况就会出现。急性焦虑在威胁结束之后，会自己慢慢消失，但是如果长期处在压力环境下，就会形成慢性焦虑，感觉每天都忧心忡忡，还会有全身的症状，比如头痛、头晕、心慌、肠胃不适、浑身无力、呼吸困难等等。 ")]),_c('a',{attrs:{"id":"id6"}},[_vm._v("恐慌")]),_c('p',[_vm._v(" 伴随着焦虑症的，还有一个极为恐怖的症状，就是恐慌发作，感觉就像自己要死了一般，心跳的飞快，喘不上气、两眼发黑、非要逃出当前这个环境才会慢慢缓解，第一次发生这种情况的人会以为心脏病发，但去急诊却又查不出毛病来。这种情况经常会发生在地铁、商场等周围声音灯光人群刺激比较大的地方。这是因为人在长期压力下，已经没有多余的能量处理周围的环境刺激了，会产生一个本能的逃跑反应，当你身体越虚弱，压力越大，这种情况就越容易发生。 ")]),_c('a',{attrs:{"id":"id7"}},[_vm._v("抑郁情绪")]),_c('p',[_vm._v(" 长期的压力还会导致情绪的低落。它是抑郁情绪，跟抑郁症还是有一点差距的，只要能够及时恢复。如果你在最近一段时间发现对什么东西都失去了兴趣，包括以前的爱好，工作上也提不起劲，白天犯困一直想躺着，晚上却又睡不着，就要注意是自己是不是已经有了抑郁情绪，尤其是当你发现对生活的激情已经消失了。 ")]),_c('a',{attrs:{"id":"id8"}},[_vm._v("睡眠质量降低")]),_c('p',[_vm._v(" 因为工作压力，很多人觉得晚上下班后才是真正属于自己的自由时间，可以尽情的去放松，于是沉溺于刷手机或者泡酒吧等放纵式的放松活动中，一不注意就到了凌晨一两点，睡 5、6 个小时又强行爬起来去上班。以前公司大部分 9 点上班，现在基本推到 10 点了，跟这个也有关系，这不是好的现象。人的生物钟是固定的，只有在适合自己的时间睡觉才会得到有效休息，如果往后推两小时才睡觉，即时睡够 8 小时，第 2 天醒来的精神状态也是不一样的。而工作压力又导致睡眠质量的下降，入睡时间长、半夜容易惊醒，就越来越难保证足够的睡眠，长期下去就会形成睡眠债（Sleep dept）。 睡眠不足的危害特别大，首先是记忆力下降。人的记忆是在睡眠中形成的，睡眠不足会影响大脑固化白天所学的东西。第二，影响你的认知能力。有好多人说过，在通宵加班几天之后，就有一种像行尸走肉的感觉，不知道自己在做什么。这样就导致很容易出差错，如果开车会增大出车祸的几率，而工作中也会导致写的 bug 越来越多。 ")]),_c('a',{attrs:{"id":"id9"}},[_vm._v("解决方法")]),_c('p',[_vm._v(" 对于心理上的压力，处理起来就比较棘手，但还是有一些有效的方法。首先应从根本上消除皮质醇。最好的办法就是运动。通过运动排汗能够把压力荷尔蒙排出，同时还能分泌一种让你感觉快乐荷尔蒙——多巴胺。这就是为什么在跑步或者进行力量训练之后，会有一种愉悦的感觉。运动不必是高强度的，一天保证运动30分钟，一周5天中等强度就可以了，而这30分钟可以匀到一整天，比如上午10分钟，下午10分钟，晚上10分钟。 另一个比较有效的方法是冥想。当你遇到问题和压力时，大脑是飞速运转的，一直在想某个问题，整个人处在一个无底的思考黑洞里，通过冥想，可以把你从思绪中拉回来，关注当下，感受周围的气氛、声音和身体感受。再随着深呼吸，慢慢的感受自己吸吸入清新的空气，呼出紧张的废气，身体就会慢慢的放松下来，产生愉悦感。关于冥想有专门进行冥想指导的APP。 良好的社交也会带来身心的放松，工作再忙也要匀出时间来跟朋友聊聊天、吹吹水。上班的时候可以抽出时间来去跟同事们交流最新的技术新闻、科技产品，也可以讨论 bug、八卦拉家常。下班的时候也可以参加活动去认识一些其他圈子里面的人，没准还能遇到未来的女朋友～ ")]),_c('a',{attrs:{"id":"id10"}},[_vm._v("最后")]),_c('p',[_vm._v(" 1024是程序员节，真正的需要关心的是自己的健康。身体是自己的，公司是别人的，累垮了便再也没有美好等着你去发现，得不偿失。没有一个人必须在一个公司里面待一辈子，被迫努力工作，赚钱养家等等。大多时候，你的努力公司并不认可，公司关注的是利益，这也是为什么好多人明明技术水平不差，却在公司里混不出一官半职，也拿不到高薪，因为老板的想法和你的想法真的不一样，你以为的并不真的就是你以为的。如果想通过卖命得到自己想要的，那就去创业。当然不排除以人为本的公司，能争取进去也是不错的。所以，在公司里面混，一定要先注意自己的身体健康，然后才是工作。人只活一次，累坏了不觉得可惜吗？从现在开始，在了解了工作压力的危害和应对方式之后，希望你能够作为参考，把爱护自己，让自己健康提上议程。 ")])])]),_c('aside',{staticClass:"right"},[_c('ul',{staticClass:"first"},[_c('li',[_c('a',{attrs:{"href":"#id1"}},[_vm._v("生理问题")]),_c('ul',{staticClass:"second"},[_c('li',[_c('a',{attrs:{"href":"#id2"}},[_vm._v("解决方法")])])])]),_c('li',[_c('a',{attrs:{"href":"#id3"}},[_vm._v("心理问题")]),_c('ul',{staticClass:"second"},[_c('li',[_c('a',{attrs:{"href":"#id4"}},[_vm._v("压力的产生机制")])]),_c('li',[_c('a',{attrs:{"href":"#id5"}},[_vm._v("焦虑")])]),_c('li',[_c('a',{attrs:{"href":"#id6"}},[_vm._v("恐慌")])]),_c('li',[_c('a',{attrs:{"href":"#id7"}},[_vm._v("抑郁情绪")])]),_c('li',[_c('a',{attrs:{"href":"#id8"}},[_vm._v("睡眠质量降低")])]),_c('li',[_c('a',{attrs:{"href":"#id9"}},[_vm._v("解决方法")])])])]),_c('li',[_c('a',{attrs:{"href":"#id10"}},[_vm._v("最后")])])])])])}]

export { render, staticRenderFns }